.study-set-card-container {
  border: 5px solid transparent;
  -webkit-column-break-inside: avoid;
     page-break-inside: avoid;
          break-inside: avoid;
  font-family: hurme_no2-webfont, -apple-system, BlinkMacSystemFont, sans-serif;
}
.study-set-card-container a{
  text-decoration: none
}

.study-set-card {
  height: 280px;
  -webkit-box-shadow: -1px 2px 6.5px 2px #D9D9D9, 1px 3px 8.5px 3px #D9D9D9;
          box-shadow: -1px 2px 6.5px 2px #D9D9D9, 1px 3px 8.5px 3px #D9D9D9;
  background: white;
  padding: 1em;
  margin-bottom: 1.3em;
  -webkit-column-break-inside: avoid;
  page-break-inside: avoid;
  break-inside: avoid;
  padding: 0;
}

.study-set-card:hover {
  -webkit-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
  border-bottom: 5px solid #FFCD1F;
  color: #FFCD1F;
}

.study-set-image {
  height: 200px;
  width: 100%;
  margin: 0;
}

.card-profile-image {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 1px solid #d9d9d9;
  margin: 10px 10px 10px 5px;
}

.set-title {
  font-size: 20px;
  line-height: 24px;
  font-family: inherit;
  font-weight: 700;
  color: #455358;
  text-align: left;
  margin-left: 20px;
}

.created-by {
  font-size: 12px;
  color: #3DCECF;
  margin: 10px;
}

.term-count {
  height: 20px;
  font-size: 12px;
  font-weight: 600;
  color: #455358;
  border-right: 1px solid #d9d9d9;
  margin: 10px;
  padding-right: 10px;
}

.created-by {
  margin-left: 10px;
}

.study-set-info-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: start;
      align-items: flex-start;
}



