.subject-search-top {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.subject-button-container {
  display: -ms-flexbox;
  display: flex;
}

.subject-search-title {
  font-size: 20px;
  color: #455358;
}

.subject-button {
  height: 35px;
  color: #3DCECF;
  background-color: white;
  border: 1px solid #d9d9d9;
  border-top: 2px solid #d9d9d9;
  padding: 10px;
  font-size: 14px;
}

.subject-button:hover {
  -webkit-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
  color: #FFCD1F;
}

.subject-button:focus {
  background-color: #FFCD1F;
  color: #6E6E4B;
  outline: 0;
}

.option-button {
  background-color: #3DCECF;
  border: none;
  border-radius: 0px;
  color: white;
  height: 45px;
  width: 100px;
  margin-left: 25px;
}



